

  @keyframes flipcard {
      0% { 
        transform:  rotateX(-180deg);
        
        box-shadow: 0 0 0; 
    }  
    10% { 
      transform: rotateX(0deg) rotateY(0deg);
    }
    97%{
      transform: rotateX(0deg) rotateY(0deg);
    }    
    
    100% { 
      transform: rotateY(180deg);
    }
  }

 .quote-card {      
  animation: flipcard 20s infinite ease-in-out; 
  backface-visibility: hidden;
  }

  .quote-card {
    border-radius: 1em 1em 1em 1em;    
    border-width: 1px;
    box-shadow: rgb(245, 250, 248) 0px 0px 250px;
    color: lightyellow;
   
    border-color: rgba(250, 248, 248, 0.1);
    margin-top: 5rem;
    margin-bottom: 2rem;
    width: 100%;
    max-width: 20rem;
    max-height: 35em;
    margin-right: auto;
    margin-left: auto;  
    background-image: linear-gradient(120deg, rgba(61, 112, 122, 0.897) 20%, #30eed8 100%);
    padding:3em;    
}

  

  